@import "../../assets/scss/setup/mixins";
@import "../../assets/scss/setup/variables";
@import "../../assets/scss/basics/typography";

.home-page {
  height: 100%;
  animation: 1s ease-out fadein;
  background: linear-gradient(90deg, rgb(230, 218, 206) 50%, white 50%);
  .inner-square {
    width: 750px;
    height: 545px;
    background: linear-gradient(90deg, rgb(244, 236, 230) 50%, white 50%);
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    border-radius: 5px;
    position: absolute;
    left: 50%;
    top: 65%;
    transform: translate(-50%, -50%);
    .about-me {
      width: 375px;
      padding: 20px 30px;
      position: relative;
      img {
        width: 206px;
        border-radius: 50%;
        margin-bottom: 30px;
      }

      h1 {
        font-family: poppins-bold;
        margin-bottom: 20px;
        font-size: 27px;
      }

      .seperator {
        width: 50px;
        height: 1.5px;
        background-color: rgb(0, 80, 255);
        margin-bottom: 25px;
      }
      h2 {
        font-family: avenir-thin;
        font-size: 17px;
        text-align: center;
        letter-spacing: 3px;
        max-width: 220px;
        line-height: 25px;
      }

      .socials {
        position: absolute;
        bottom: 0;
        background-color: white;
        height: 50px;
        width: 100%;
        a {
          font-size: 20px;
          padding: 0 10px;
        }
      }
    }

    .left-side {
      margin-left: 30px;
      .hello {
        font-family: poppins-bold;
        font-size: 100px;
        margin-bottom: 10px;
      }

      .sec-p {
        font-family: avenir-semibold;
        font-size: 20px;
        margin-bottom: 30px;
      }

      .btns-container {
        margin-bottom: 55px;
        .btn {
          font-family: avenir-semibold;
          font-size: 14px;
          letter-spacing: 0.5px;
          width: 130px;
          border-radius: 25px;
          border: 1px solid black;
          padding: 8px 12px;
          background-color: transparent;
          outline: none;
          transition: all ease-in-out 0.3s;

          &.resume {
            background-color: rgb(0, 80, 255);
            border: 1px solid transparent;
            color: white;
            margin-right: 10px;
            &:hover {
              background-color: transparent;
              border: 1px solid black;
              a {
                font-family: avenir-semibold;
                color: black;
              }
            }
            a {
              font-size: 14px;
              color: white;
            }
          }
          &.projects {
            font-family: avenir-reg;

            &:hover {
              font-family: avenir-semibold;
              background-color: rgb(0, 80, 255);
              color: white;
              border: none;
            }
          }
        }
      }

      a {
        font-family: avenir-reg;
        color: rgb(0, 80, 255);
        transition: all ease-in-out 0.3s;
        font-size: 20px;
        letter-spacing: 0.5px;
        position: relative;
        width: fit-content;
        .fa-arrow-right {
          font-weight: 100 !important;
          font-size: 15px;
          position: absolute;
          right: -15px;
          top: 2px;
        }
        span {
          margin-right: 5px;
        }
        &:hover {
          transform: scale(1.05);
        }
      }
    }
  }
}
